'use client';

import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

const testData = [
    { name: 'Mon', value: 65 },
    { name: 'Tue', value: 75 },
    { name: 'Wed', value: 85 },
    { name: 'Thu', value: 70 },
    { name: 'Fri', value: 90 },
];

export const TestChart = () => {
    console.log('TestChart rendering with data:', testData);

    return (
        <div style={{ width: '100%', height: '300px', background: '#1a1a1a', padding: '20px' }}>
            <h3 style={{ color: 'white', marginBottom: '10px' }}>Test Chart (Should show a line)</h3>
            <ResponsiveContainer width="100%" height="100%">
                <LineChart data={testData} margin={{ top: 20, right: 30, left: 20, bottom: 20 }}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#444" />
                    <XAxis dataKey="name" stroke="#999" />
                    <YAxis stroke="#999" />
                    <Tooltip />
                    <Line type="monotone" dataKey="value" stroke="#0ea5e9" strokeWidth={2} />
                </LineChart>
            </ResponsiveContainer>
        </div>
    );
};
